@charset "UTF-8";
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Helvetica"), url("../fonts/helvetica-regular.woff2") format("woff2"), url("../fonts/helvetica-regular.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Helvetica"), url("../fonts/helvetica-bold.woff2") format("woff2"), url("../fonts/helvetica-bold.woff") format("woff");
}
.container {
  width: 100%;
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    padding: 0 15px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  font-size: 10px;
  /* Если мы используем проценты(а не пиксели) и rem, то у нас будет зависимость от размера шрифта установленного в браузере. Т.е. если пользователь будет увеличивать щрифт и мы указали в процентах, то все будет увеличиваься, но может и ломаться верстка  */
  /* Тоже самое что и 10px. Дефолтный размер в браузере - 16px это 100% */
  font-size: 62.5%;
}

.page__body {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  background-color: #fff;
  color: #000000;
}
@media (max-width: 767px) {
  .page__body {
    /* Для темного фона */
    position: relative;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.page__body.shadow::before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(3, 16, 27, 0.7);
  z-index: 4;
  transition: background-color 0.3s linear;
}

.page__body.scroll-lock {
  overflow: hidden;
}

/* Сброс дефолтных стилей у списков */
/* Медиа запрос */
.header {
  border-bottom: 0.5px solid #C8C8C8;
  margin-bottom: 4.8rem;
}
@media (max-width: 1200px) {
  .header {
    border-bottom: none;
    margin-bottom: 2.2rem;
    /* Для меню в мобилке */
    position: relative;
    /* Плашка */
    /* Выезжающий фон - справа у мобильного меню */
  }
  .header::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    background-color: #000;
    z-index: 6;
    transition: height 0.3s linear;
  }
  .header::before {
    content: "";
    position: absolute;
    top: 6rem;
    right: -50%;
    width: 50%;
    height: 0;
    background-color: #000;
    transition: right 0.3s linear, height 0.3s linear;
    z-index: 5;
  }
}

.header__wrapper {
  display: grid;
  grid-template-columns: 1fr min-content 1fr;
  align-items: center;
  align-items: center;
  padding: 2.5rem 0;
}
@media (max-width: 1200px) {
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 2.5rem 0 0;
  }
}

/* Добавление плашки при открытии меню, чтобы при большом количестве эл-ов навигации в мобильном меню ничего не заезжало на бургер*/
.header.is-open::after {
  height: 6rem;
}

.header.is-open::before {
  right: 0;
  height: 100vh;
}

.header__nav-block {
  justify-self: end;
  margin-left: 5rem;
}
@media (max-width: 1200px) {
  .header__nav-block {
    margin: 0;
  }
}

/* header__search-modal */
.header .container {
  position: relative;
}

.site-nav {
  display: block;
  margin-right: 5rem;
}
@media (max-width: 1200px) {
  .site-nav {
    display: block;
    position: fixed;
    margin-right: 0;
    /* Растягиваем на всю ширину и прижимаем к верху*/
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    /* Прокручивание когда не помещаются эл-ты по высоте */
    height: 100%;
    overflow: auto;
    padding: 6rem 0 0;
    z-index: 5;
    transition: top 0.3s linear, transform 0.3s linear;
    /* Выезжающий фон - слева у мобильного меню */
  }
  .site-nav::after {
    content: "";
    position: absolute;
    top: 6rem;
    right: 50%;
    width: 50%;
    height: 0;
    background-color: #000;
    transition: height 0.3s linear;
  }
}

.site-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 4rem;
  row-gap: 2rem;
}
.site-nav__list--mobile {
  display: none;
}
@media (max-width: 1200px) {
  .site-nav__list {
    display: block;
    text-align: center;
    position: relative;
    z-index: 5;
    padding: 0 0 2rem;
  }
  .site-nav__list--mobile {
    display: block;
    padding: 0 0 2rem;
  }
}

.site-nav__item a {
  font-size: 1.4rem;
  line-height: 1.3;
  color: #000;
  text-decoration: none;
}
@media (max-width: 1200px) {
  .site-nav__item a {
    display: block;
    color: #fff;
    text-transform: uppercase;
    padding: 1rem 0;
    font-size: 1.8rem;
    line-height: 1.2;
  }
}
@media (min-width: 1200px) {
  .site-nav__item a:hover, .site-nav__item a:focus {
    color: #cc7e5e;
  }
  .site-nav__item a:active {
    color: #f68050;
  }
}
.site-nav__item a:active {
  color: #f68050;
}

@media (max-width: 1200px) {
  .site-nav.is-open {
    top: 0;
    transform: translateX(0);
  }
}

@media (max-width: 1200px) {
  .site-nav.is-open::after {
    height: 100vh;
  }
}

.logo {
  display: inline-block;
  vertical-align: middle;
  width: 10rem;
  height: 2.2rem;
  fill: #000;
}
@media (max-width: 1200px) {
  .logo.is-open {
    fill: #fff;
    z-index: 7;
  }
}
@media (min-width: 1200px) {
  .logo:hover, .logo:focus {
    fill: #cc7e5e;
  }
  .logo:active {
    fill: #f68050;
  }
}
.logo:active {
  fill: #f68050;
  transition-duration: 0s;
}

.logo svg {
  width: 100%;
  height: 100%;
}

@media (max-width: 1200px) {
  .user-nav {
    display: inline-block;
    vertical-align: middle;
  }
}

.user-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 3rem;
  row-gap: 2rem;
}

/* Скрыл все элементы кроме последнего */
.user-nav__item {
  display: block;
}
@media (max-width: 1200px) {
  .user-nav__item {
    display: none;
  }
  .user-nav__item--cart {
    display: block;
  }
}

.user-nav__link {
  display: inline-block;
  vertical-align: middle;
  width: 2rem;
  height: 2rem;
  position: relative;
}
@media (max-width: 1200px) {
  .user-nav__link--cart {
    width: 2.8rem;
    height: 2.8rem;
  }
}

@media (max-width: 1200px) {
  .user-nav__link--cart span {
    display: none;
  }
}

.user-nav__link svg {
  width: 100%;
  height: 100%;
}

.user-nav__link span {
  position: absolute;
  top: -3px;
  right: -7px;
  font-size: 1rem;
  /* Если при вёрстке макета вы видите в графическом редакторе межстрочное расстояние, заданное в пикселях, то не стоит переносить его в точности. В случае, если размер шрифта элемента изменится, то абсолютно заданное межстрочное расстояние не подстроится. А хотелось бы больше гибкости.*/
  line-height: 0.8;
  width: 1.7rem;
  height: 1.6rem;
  text-align: center;
  padding: 4px;
  background-color: #000000;
  color: #ffffff;
  border-radius: 50%;
  pointer-events: none;
}
@media (max-width: 1200px) {
  .user-nav__link span {
    display: none;
  }
}

.user-nav__link.user-nav__link--btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
  stroke: #000000;
  fill: none;
}
@media (min-width: 1200px) {
  .user-nav__link.user-nav__link--btn:hover, .user-nav__link.user-nav__link--btn:focus {
    stroke: #cc7e5e;
  }
  .user-nav__link.user-nav__link--btn:active {
    stroke: #f68050;
  }
}
.user-nav__link.user-nav__link--btn:active {
  stroke: #f68050;
}

.burger {
  display: none;
}
@media (max-width: 1200px) {
  .burger {
    display: inline-block;
    vertical-align: middle;
    width: 3.2rem;
    height: 3.2rem;
    margin-left: 2.4rem;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    z-index: 7;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    height: 1.5px;
    background-color: #000;
    right: 0;
    transition: background-color 0.3s linear;
  }
  .burger::before {
    width: 3.2rem;
    top: 12px;
    transition: transform 0.3s linear;
  }
  .burger::after {
    width: 1.8rem;
    top: 21px;
    transition: top 0.3s linear, transform 0.3s linear;
  }
}

/* При открытом меню */
.burger.is-open::before,
.burger.is-open::after {
  background-color: #fff;
}

.burger.is-open::before {
  transform: rotate(45deg);
}

.burger.is-open::after {
  top: 12px;
  width: 3.2rem;
  transform: rotate(-45deg);
}

.search-modal {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 5;
  padding: 0 20px;
  /* Скрываем эл-т */
  overflow: hidden;
  max-height: 0;
  /* Не работает тогда focus() в js */
  visibility: hidden;
  transition: max-height 0.3s linear, visibility 0.3s linear;
}

.search-modal__container {
  display: grid;
  grid-template-columns: 1fr min-content;
  background-color: #e8a98f;
  border-radius: 0 0 10px 10px;
  padding: 10px;
}

.search-modal form {
  justify-self: center;
}

.search-modal .promo-code {
  margin: 0;
}

.search-modal input {
  border: none;
  border-radius: 0 0 15px 15px;
}

.search-modal__close {
  position: relative;
  width: 22px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  user-select: none;
}
.search-modal__close::before, .search-modal__close::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  background-color: #000;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.search-modal__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.search-modal__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}
@media (min-width: 1200px) {
  .search-modal__close:hover::after, .search-modal__close:focus::after, .search-modal__close:hover::before, .search-modal__close:focus::before {
    background-color: #d75757;
  }
  .search-modal__close:active::after, .search-modal__close:active::before {
    background-color: #f00;
  }
}
.search-modal__close:active::after, .search-modal__close:active::before {
  background-color: #f00;
}

.breadcrumbs {
  margin-bottom: 4.2rem;
}
@media (max-width: 1200px) {
  .breadcrumbs {
    margin-bottom: 3.2rem;
  }
}

.breadcrumbs__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "⁄";
  display: inline-block;
  position: relative;
  padding: 0 1rem;
  color: #6E6E6E;
  transform: rotate(-13deg) translate(-3px, -1px);
}

.breadcrumbs__item a {
  font-size: 1.2rem;
  line-height: 1.25rem;
  color: #6E6E6E;
  text-decoration: none;
}
@media (min-width: 1200px) {
  .breadcrumbs__item a:hover, .breadcrumbs__item a:focus {
    color: #000000;
  }
  .breadcrumbs__item a:active {
    color: #f68050;
  }
}
.breadcrumbs__item a:active {
  color: #f68050;
}

.breadcrumbs__item.is-current a {
  color: #000000;
  cursor: default;
}

.authorization-link {
  align-self: end;
}

.authorization-link span {
  line-height: 1.3rem;
  cursor: default;
}

.authorization-link button {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 1.3rem;
  padding: 0.2rem;
  cursor: pointer;
  color: #6E6E6E;
  background-color: transparent;
  border: none;
}
@media (min-width: 1200px) {
  .authorization-link button:hover, .authorization-link button:focus {
    color: #000000;
  }
  .authorization-link button:active {
    color: #f68050;
  }
}
.authorization-link button:active {
  color: #f68050;
}

/* Позиционирование блоков */
.making-order__wrapper {
  display: grid;
  grid-template-columns: 63.3% 33.4%;
  column-gap: 4rem;
  margin-bottom: 5rem;
}
@media (max-width: 1200px) {
  .making-order__wrapper {
    display: block;
  }
}

.making-order__approximate-cost {
  grid-column: 1/2;
}

.making-order__product-list {
  grid-column: 1/2;
}

.making-order__header {
  display: flex;
  column-gap: 2rem;
  margin: 0 0 6rem;
}
@media (max-width: 1200px) {
  .making-order__header {
    display: block;
    margin: 0 0 3.6rem;
  }
}

.making-order__header h2 {
  font-size: 4rem;
  line-height: 1.05;
  font-weight: 400;
  margin: 0;
}
@media (max-width: 1200px) {
  .making-order__header h2 {
    font-size: 2.8rem;
    line-height: 1.07;
    font-weight: 400;
    margin: 0 0 1rem;
  }
}

/* Товары */
.making-order__product-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;
}
@media (max-width: 1200px) {
  .making-order__product-list {
    row-gap: 1.5rem;
  }
}

.product__description h3 {
  font-size: 2rem;
  line-height: 1.2;
  margin: 0 0 1rem;
}

/* cheque */
.making-order__cheque {
  grid-row: 1/2;
  grid-column: 2/-1;
}

/* recovery */
.recovery {
  width: 100%;
  height: 50px;
}

.approximate-cost {
  display: flex;
  column-gap: 0.8rem;
  flex-wrap: wrap;
  font-size: 2.8rem;
  line-height: 1.07;
  margin: 0 0 2.7rem;
}
@media (max-width: 1200px) {
  .approximate-cost {
    margin: 0 0 2rem;
  }
}

.approximate-cost p {
  margin: 0;
}

.map {
  width: 100%;
  max-width: 400px;
  height: 450px;
  background-color: rgb(77, 146, 173);
  position: absolute;
  top: 0;
  right: 0;
}
@media (max-width: 1200px) {
  .map {
    position: static;
    width: 100%;
    max-width: none;
    height: 22rem;
    margin: 1.6rem 0 4.3rem;
  }
}

.cheque {
  width: 100%;
  max-width: 400px;
  border: 1px solid #6E6E6E;
}
@media (max-width: 1200px) {
  .cheque {
    width: 100%;
    max-width: none;
    min-height: 100%;
    margin: 0 0 4rem;
  }
}

.cheque__submit-block {
  padding: 0 2rem;
}
@media (max-width: 1200px) {
  .cheque__submit-block {
    padding: 0 1.6rem;
  }
}

.cheque__btn-submit {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
  font-weight: 700;
  width: 100%;
  padding: 1.5rem 3rem;
  margin: 0 0 1rem;
  color: #fff;
  background-color: #000;
  border: none;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .cheque__btn-submit:hover, .cheque__btn-submit:focus {
    background-color: #997a6d;
  }
  .cheque__btn-submit:active {
    background-color: #64493e;
  }
}
.cheque__btn-submit:active {
  background-color: #64493e;
}

.delivery {
  margin: 0 2rem;
  /* Для кастомного checkbox */
  position: relative;
}
@media (max-width: 1200px) {
  .delivery {
    margin: 0 1.5rem;
  }
}

.delivery label {
  cursor: pointer;
  display: block;
  padding: 2rem 0 2.5rem 0;
  border-top: 1px solid #C8C8C8;
  border-bottom: 1px solid #C8C8C8;
}
@media (max-width: 1200px) {
  .delivery label {
    padding: 1.4rem 0 1.1rem;
  }
}

.delivery__info {
  padding: 0 4.5rem 0 0;
}

.delivery__info p {
  font-size: 2rem;
  line-height: 1.2;
  margin: 0;
}
@media (max-width: 1200px) {
  .delivery__info p {
    font-size: 1.6rem;
    line-height: 1.3;
    margin: 0 0 0.7rem;
  }
}

.delivery__info span {
  font-size: 1.2rem;
  line-height: 1.25;
  color: #6E6E6E;
}

.delivery__checkbox-indicator {
  position: absolute;
  right: 0;
  top: 2.5rem;
  width: 3.4rem;
  height: 1.4rem;
  background-color: #C8C8C8;
  border-radius: 5rem;
  opacity: 0.5;
  transition: background-color 0.3s linear;
}
@media (max-width: 1200px) {
  .delivery__checkbox-indicator {
    top: 2rem;
  }
}
.delivery__checkbox-indicator::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 50%;
  background-color: #6E6E6E;
  transition: right 0.3s linear, background-color 0.3s linear;
}

.delivery input:checked + .delivery__checkbox-indicator {
  background-color: green;
}

.delivery input:checked + .delivery__checkbox-indicator::after {
  right: 0;
  background-color: green;
}

@media (min-width: 1200px) {
  .delivery label:hover p:hover, .delivery label:hover p:focus {
    color: #cc7e5e;
  }
  .delivery label:hover p:active {
    color: #f68050;
  }
}
.delivery label:hover p:active {
  color: #f68050;
}

.total-cost {
  display: flex;
  justify-content: space-between;
  font-size: 2.8rem;
  line-height: 1.07;
  font-weight: 700;
  padding: 2rem;
}
@media (max-width: 1200px) {
  .total-cost {
    font-size: 2rem;
    line-height: 1.2;
    padding: 1.2rem 1.9rem 1.2rem 1.5rem;
  }
}

.total-cost__number {
  display: flex;
  column-gap: 0.5rem;
}

.total-cost p,
.total-cost__number p {
  margin: 0;
}

.promo-code {
  position: relative;
  margin: 0 0 2rem 0;
}
@media (max-width: 1200px) {
  .promo-code {
    margin: 0 0 1.2rem 0;
  }
}

.cheque__promo-code.promo-code {
  margin: 0 2rem 1.7rem;
}
@media (max-width: 1200px) {
  .cheque__promo-code.promo-code {
    margin: 0 1.6rem 1.2rem;
  }
}

.promo-code input {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
  width: 100%;
  padding: 1.5rem 11.5rem 1.5rem 2rem;
}
@media (max-width: 1200px) {
  .promo-code input {
    padding: 1rem 8rem 1rem 1.6rem;
  }
}

.promo-code input::placeholder {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
}

.promo-code__btn {
  font-family: "Helvetica", "Arial", sans-serif;
  line-height: 1.3;
  font-weight: 700;
  /* Область клика */
  height: 100%;
  padding: 0 1.6rem 0 1.6rem;
  /* Когда кнопку не видно - нет доступа через клавиатуру */
  visibility: hidden;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border: none;
