.promo-code {
  position: relative;

  margin: 0 0 2rem 0;

  @include breakpoint($vp-767) {
    margin: 0 0 1.2rem 0;
  }
}

.cheque__promo-code.promo-code {
  margin: 0 2rem 1.7rem;

  @include breakpoint($vp-767) {
    margin: 0 1.6rem 1.2rem;
  }
}

.promo-code input {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
  width: 100%;
  padding: 1.5rem 11.5rem 1.5rem 2rem;

  @include breakpoint($vp-767) {
    padding: 1rem 8rem 1rem 1.6rem;
  }
}

.promo-code input::placeholder {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
}

.promo-code__btn {
  font-family: "Helvetica", "Arial", sans-serif;
  // font-size: 1.6rem;
  line-height: 1.3;
  font-weight: 700;

  /* Область клика */
  height: 100%;
  padding: 0 1.6rem 0 1.6rem;

  /* Когда кнопку не видно - нет доступа через клавиатуру */
  visibility: hidden;

  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);

  border: none;
  background-color: transparent;

  cursor: pointer;

  /* Скрываем кнопку */
  font-size: 0;
  opacity: 0;

  transition: opacity 0.3s ease, font-size 0.3s ease;

  @include breakpoint($vp-767) {
    font-size: 1.4rem;
    line-height: 1.14;
    padding: 0 1rem 0 0;
  }

  // Эффекты при наведении в десктопной версии
  @media (min-width: 1200px) {

    &:hover,
    &:focus {
      color: #cc7e5e;
    }

    &:active {
      color: #f68050;
    }
  }

  &:active {
    color: #f68050;
  }
}

/* Показываем кнопку */
.promo-code__btn.show {
  font-size: 1.6rem;
  opacity: 1;
  visibility: visible;
}

/* Смс об ошибке/удаче :) */

.promo-code__message {
  display: flex;

  font-size: 1.2rem;
  line-height: 1.25;

  margin: 0.4rem 1.6rem 0;
}

.promo-code__message p {
  margin: 0;
}
